import React from 'react';
import {Col, ListGroup, Row, Tab} from "react-bootstrap";
import Profile from "./Profile";
import MyCard from "./MyCard";
import MyStack from "./MyStack";



function MyPage(props) {

    return(
        <Tab.Container defaultActiveKey="#profile">
            <Row>
                <Col sm={1}></Col>
                <Col sm={2} className={'my-5 text-start'}>
                    <ListGroup id={'list-group-myPage'}>
                        <ListGroup.Item className={'py-3'} variant={'light'} action href="#profile">
                             
                        </ListGroup.Item>
                        <ListGroup.Item className={'py-3'} variant={'light'} action href="#project">
                            
                        </ListGroup.Item>
                        <ListGroup.Item className={'py-3'} variant={'light'} action href="#solution">
                             
                        </ListGroup.Item>
                        <ListGroup.Item className={'py-3'} variant={'light'} action href="#question">
                             / 
                        </ListGroup.Item>
                    </ListGroup>
                </Col>
                <Col sm={8}>
                    <Tab.Content>
                        <Tab.Pane eventKey="#profile">
                            <h2 className={'text-start ms-5 mt-5'}> </h2>
                            <MyCard title={''}>
                                <Profile />
                            </MyCard>
                            <MyCard title={''}>

                            </MyCard>
                            <MyCard title={' /  '}>
                                <MyStack />
                            </MyCard>
                            <MyCard title={' '}>

                            </MyCard>
                        </Tab.Pane>
                        <Tab.Pane eventKey="#project">
                            <h2 className={'text-start ms-5 mt-5'}></h2>
                            <MyCard title={'  '}>

                            </MyCard>
                            <MyCard title={'  '}>

                            </MyCard>
                        </Tab.Pane>
                        <Tab.Pane eventKey="#solution">
                            <h2 className={'text-start ms-5 mt-5'}> </h2>
                            <MyCard title={' '}>

                            </MyCard>
                        </Tab.Pane>
                        <Tab.Pane eventKey="#question">
                            <h2 className={'text-start ms-5 mt-5'}> / </h2>
                            <MyCard title={'  '}>

                            </MyCard>
                            <MyCard title={'  '}>

                            </MyCard>
                        </Tab.Pane>
                    </Tab.Content>
                </Col>
            </Row>
        </Tab.Container>
    )
}

export default MyPage;