import React, {useState} from 'react';

function Profile(props) {
    const [mode, setMode] = useState(true)


    return (
        <>
            {/*   p       */}
            {
                mode
                    ? <button onClick={() => setMode(!mode)} type={'button'} className={'float-end theme-btn px-3'}></button>
                    : <>
                        <button onClick={() => setMode(!mode)} type={'button'} className={'float-end theme-btn ms-2 px-3'}></button>
                        <button onClick={() => setMode(!mode)} type={'button'} className={'float-end theme-outline-btn px-3'}></button>
                    </>
            }

        </>
    )
}

export default Profile;