import React, {useEffect, useState,} from 'react';
import {Form, InputGroup} from "react-bootstrap";
import DisabledButton from "./DisabledButton";
import axios from "axios";

function SignupForm(props) {


    //  
    const [mailVal, setMailVal] = useState("");

    //  input on/off 
    const [authEl, setAuthEl] = useState(false);

    //  
    const [authCode, setAuthCode] = useState("");

    //  
    const [codeVal, setCodeVal] = useState("");

    //   readonly 
    const [authDone, setAuthDone] = useState(false);

    //  
    const [nick, setNick] = useState("");

    //    
    const [nickText, setNickText] = useState(0);

    //  
    const [password1, setPassword1] = useState("");
    const [password2, setPassword2] = useState("");

    //    
    const [passwordText, setPasswordText] = useState(false);

    const [disabled, setDisabled] = useState(true);

    const MatchCode = () => {

        if (authCode === codeVal) {
            //   
            alert('.')
            //   off
            setAuthEl(false);
            // readonly  on
            setAuthDone(true);
        } else {
            //   alert
            alert('  ')
        }
    };


    //    &   
    const EmailAuth = () => {
        // mailVal    
        if (mailVal) {
            // authEl    
            let timer = setTimeout(() => setAuthEl(true), 200)

            axios.post('http://localhost:8080/emailConfirm', null, {
                params: {
                    email: mailVal,
                }
            })
                .then((resp) => {

                    //    alert
                    if (resp.data == 1) {
                        alert('  .');
                        clearTimeout(timer);
                    } else {
                        //     (   )

                        //   
                        setAuthCode(resp.data);
                        //  
                    }

                })
                .catch(() => {
                    //    err 
                    alert(' .');

                });
        }
    };

    let nickTimer;

    useEffect(() => {
        clearTimeout(nickTimer);
        // nick  clear
        if (nick === "") {
            setNickText(0);
        } else {
            nickTimer = setTimeout(() => {
                axios.post('http://localhost:8080/checkNick', null, {
                    params: {
                        nickName: nick,
                    }
                })
                    .then((resp) => {
                        //   
                        if (resp.data === 1) {
                            // p  , 
                            setNickText(1);
                        } else {
                            //   
                            setNickText(2);
                        }
                    })
                    .catch((err) => {
                        alert(err);
                    });
            }, 100);
        }
    }, [nick]);

    useEffect(() => {
        if (!password1 || !password2) {
            setPasswordText(false);
        } else if (password2 && password1 === password2) {
            // password  
            setPasswordText(true);
        } else {
            setPasswordText(false);
        }
    }, [password2]);

    //     
    useEffect(() => {
        if (authDone && nickText === 2 && passwordText) {
            setDisabled(false);
        } else {
            setDisabled(true);
        }
    }, [authDone, nickText, passwordText]);

    return (
        <Form action="http://localhost:8080/insertPerson" id={'frm'} method={'POST'}>
            <p className={'text-start mt-3'}><strong></strong></p>
            <InputGroup className="my-2">
                <InputGroup.Text id="basic-addon1"><i className="bi bi-envelope"></i></InputGroup.Text>
                <Form.Control
                    placeholder=" "
                    aria-describedby="basic-addon1"
                    name={'personId'}
                    readOnly={authDone}
                    onChange={e => {
                        setMailVal(e.target.value)
                    }}
                />
                {/*   -    disabled */}
                {
                    authDone
                        ? <button type={'button'} className={'theme-btn-disabled py-2'} disabled='disabled'>
                            </button>
                        : <button type={'button'} className={'theme-outline-btn'} onClick={() => EmailAuth()}>
                            </button>
                }
            </InputGroup>
            {/*   -        */}
            {
                authEl ?
                    <InputGroup className="my-2">
                        <InputGroup.Text id="basic-addon2"><i className="bi bi-envelope"></i></InputGroup.Text>
                        <Form.Control
                            placeholder=" "
                            aria-describedby="basic-addon2"
                            onChange={e => {
                                setCodeVal(e.target.value)
                            }}
                        />
                        <button type={'button'} className={'theme-btn'} onClick={() => MatchCode()}></button>
                    </InputGroup>
                    : ""
            }

            <p className={'text-start mt-3'}><strong></strong></p>
            <InputGroup className="my-2">
                <InputGroup.Text id="basic-addon1"><i className="bi bi-emoji-smile"></i></InputGroup.Text>
                <Form.Control
                    placeholder=" "
                    aria-describedby="basic-addon1"
                    onChange={e => setNick(e.target.value)}
                    name={'personNickName'}
                />
            </InputGroup>
            {
                nickText !== 0
                    //  
                    ? (nickText === 1
                        // 1
                        ? <p className={'text-danger text-start'}>   .</p>
                        // 2
                        : <p className={'theme-font text-start'}>  .</p>)
                    // 
                    : ""

            }


            <div className={'my-5'}>
                <p className={'text-start mb-2'}><strong></strong></p>
                <InputGroup className="">
                    <InputGroup.Text id="basic-addon2"><i className="bi bi-lock"></i></InputGroup.Text>
                    <Form.Control
                        type={'password'}
                        placeholder=" "
                        aria-describedby="basic-addon2"
                        onChange={e => setPassword1(e.target.value)}
                    />
                </InputGroup>
                <p className={'text-start my-2'}><strong> </strong></p>
                <InputGroup className="">
                    <InputGroup.Text id="basic-addon3"><i className="bi bi-lock"></i></InputGroup.Text>
                    <Form.Control
                        type={'password'}
                        placeholder=" "
                        aria-describedby="basic-addon3"
                        onChange={e => setPassword2(e.target.value)}
                        name={'personPassword'}
                    />
                </InputGroup>
                {password2
                    ? (passwordText
                        ? ""
                        : <p className={'text-danger text-start'}>  .</p>)
                    : ""}
            </div>
            <div className={'d-grid my-5'}>
                <DisabledButton btnText={''} disabled={disabled}/>
            </div>
        </Form>
    );
}

export default SignupForm;